
import jet from "./jet";
import Obj from "./types/Bol";
import Bol from "./types/Bol.js";
import Num from "./types/Num.js";
import Str from "./types/Str.js";
import Symbol from "./types/Symbol";
import Fce from "./types/Fce.js";
import RegExp from "./types/RegExp.js";
import Date from "./types/Date.js";
import NaN from"./types/NaN.js";
import Err from "./types/Err.js";
import Prom from "./types/Prom.js";
import Arr from "./types/Arr.js";
import Set from "./types/Set.js";
import Map from "./types/Map.js";

import ComplexConstructor from "./Complex";

//CUSTOM
const type = jet.type;

const Complex = jet.type.define("Complex", ComplexConstructor);

//CONVERSIONS
jet.Str.to.define({
    Fce:str=>_=>str,
    Bol:str=>!["0", "false", "null", "undefined", "NaN"].includes(str.toLowerCase()),
    Arr:(str, comma)=>str.split(comma),
    Num:(str, strict)=>Number(strict ? str : ((str.match(jet.RegExp.lib.num) || []).join("")).replace(",", ".")),
    Obj:str=>jet.Obj.json.from(str),
    Prom:async str=>str,
});

jet.Num.to.define({
    Fce:num=>_=>num,
    Bol:num=>!!num,
    Arr:(num, comma)=>comma?[num]:Array(num),
    Prom:async num=>num,
    Str:num=>String(num)
});

jet.Obj.to.define({
    Fce:obj=>_=>obj,
    Symbol:obj=>Symbol(jet.Obj.json.to(obj)),
    Bol:obj=>jet.Obj.isFull(obj),
    Num:obj=>Object.values(obj),
    Arr:obj=>Object.values(obj),
    Str:obj=>jet.Obj.json.to(obj),
    Prom:async obj=>obj,
    Err:obj=>jet.Obj.json.to(obj),
    RegExp:(obj, comma)=>jet.Map.melt(obj, comma != null ? comma : "|")
});

jet.Arr.to.define({
    Fce:arr=>_=>arr,
    Bol:arr=>jet.Arr.isFull(arr),
    Num:arr=>arr.length,
    Str:(arr, comma)=>jet.Map.melt(arr, comma),
    Obj:arr=>Object.assign({}, arr),
    Prom: async arr=>arr,
    Err:(arr, comma)=>jet.Map.melt(arr, comma != null ? comma : " "),
    RegExp:(arr, comma)=>jet.Map.melt(arr, comma != null ? comma : "|")
});

jet.Set.to.define({
    "*":set=>Array.from(set),
    Fce:set=>_=>set,
    Bol:set=>jet.Set.isFull(set),
    Obj:set=>jet.Obj.merge(set),
    Prom:async set=>set,
});

jet.Prom.to.define({
    eng:(prom, timeout)=>jet.eng(prom, timeout)
});

jet.Fce.to.define({
    "*":(fce, ...args)=>fce(...args),
    Prom:async (fce, ...args)=>await fce(...args),
});

jet.NaN.to.define(_=>undefined);

export default jet;
export {
    type,
    Obj,
    Bol,
    Num,
    Str,
    Symbol,
    Fce,
    RegExp,
    Date,
    NaN,
    Err,
    Prom,
    Arr,
    Set,
    Map,
    Complex,
}